html {
    height: 100%;
}

body {
    background: #59ba4c; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, #59ba4c 0%,#92ea81 20%,#bbf7c8 35%,#bbf7c8 35%,#bbf7c8 65%,#92ea81 80%,#59ba4c 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#59ba4c', endColorstr='#59ba4c',GradientType=1 ); /* IE6-9 */
    margin: 0;
    padding-right: 10%;
    padding-left: 10%;
}
.app-header {
    text-align: center;
}

.app-header h1 {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    font-family:'Quicksand', sans-serif;
    font-weight: bold;
    text-shadow: 0px 1px 1px white;
    margin:30px 0 20px 0;
}

.app-header h2 {
    font-size: 1.2rem;
    color: rgb(65, 77, 15);
    text-align: right;
}
.search-input {
    width: auto;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    margin: 0 10px 0 0;
    padding: 6px 12px;
    border: 1px solid #ced4da;
    border-radius: .25rem;
}

@media screen and (max-width: 615px) {
    .search-input{
        width: 100%;
    }
}
.todo-list-item {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}


.todo-list-item button {
    width: 35px;
    height: 35px;
    margin: 3px;
    padding: 0;
}

.todo-list-item-label {
    font-size: 1.25rem;
    margin-left: 1.25rem;
    line-height: 35px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    word-wrap: break-word;
}


.todo-list-item.done .todo-list-item-label{
   text-decoration: line-through;
}

.todo-list-item.important .todo-list-item-label{
    font-weight: bold;
    color: rgb(16, 105, 56);
 }

 
@media screen and (max-width: 615px) {
    .todo-list-item button {
        width: 25px;
        height: 25px;
        margin: 3px;
    }
    .todo-list-item-label {
        width: 60%;
        line-height: 20px;
        font-size: 1rem;
    }
}
.todo-list .list-group-item{
    padding: .10rem .50rem;
    width: 100%;
}
.btn-status{
    margin: 10px 0;
}


@media screen and (max-width: 615px) {
    div.btn-group{
       display: block;
       text-align: center;
    }
    .btn-status{
        padding: 6px 35px;
    }
}

@media screen and (max-width: 519px) {
    div.btn-group{
       display: block;
       text-align: center;
    }
    .btn-status{
        padding: 6px 10px;
    }
}
.item-add-form{
    margin-top: 10px;

}

.item-add-form input{
    margin-right: 5px;

}

.buttonAdd {
    font-size: 16px;
    width: 120px;
    margin-top:0; 
}
